body{
  background: #ff9966;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #ff5e62, #ff9966);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#ff5e62), to(#ff9966));
  background: -webkit-linear-gradient(right, #ff5e62, #ff9966);
  background: -o-linear-gradient(right, #ff5e62, #ff9966);
  background: linear-gradient(to left, #ff5e62, #ff9966); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;   
  text-align: center; 
}
*{
  padding: 0;
  margin: 0;
}
.notesHeader,.notesBody,.notesFooter{
  background-color: rgba(0,0,0,.5);
  padding: 20px;
  margin: 0 auto;
}
.notesHeader{
  font-family: 'Lobster', cursive;
  font-size: 1.5em;
}
.notesBody{
  margin-top: 20px;
  cursor: pointer;
}
.notesFooter{
  margin-top: 20px;
}


